"use client";

import { useMutation, useQuery, useQueryClient } from "@tanstack/react-query";
import { useIframeSdk } from "@whop/react";
import Link from "next/link";
import { useEffect, useMemo, useState } from "react";
import { Button, Card, TextField, Dialog } from "frosted-ui";
import { useRouter } from "next/navigation";
import { useWhop } from "~/components/whop-context";
import { receiptsQuery } from "~/components/whop-context/whop-queries";
import { assignPlanMutation, createPlanMutation, plansQuery, updatePlanMutation, usersQuery } from "~/components/workouts/queries";

export default function Page() {
  const iframeSdk = useIframeSdk();
  const { experience, user, access } = useWhop();
  const isAdmin = access.accessLevel === "owner" || access.accessLevel === "admin";
  const [newPlanOpen, setNewPlanOpen] = useState(false);
  const [newPlanTitle, setNewPlanTitle] = useState("");
  const [assignUserId, setAssignUserId] = useState("");
  const [editPlanId, setEditPlanId] = useState<string | null>(null);
  const [editPlanTitle, setEditPlanTitle] = useState("");
  const [userSearch, setUserSearch] = useState("");
  const qc = useQueryClient();
  const router = useRouter();

  const { data: plans, isLoading: isLoadingPlans } = useQuery(plansQuery(experience.id));
  const { data: users, isLoading: isLoadingUsers } = useQuery({ ...usersQuery(experience.id, userSearch), enabled: isAdmin });

  const createPlan = useMutation({
    mutationFn: createPlanMutation(experience.id).mutationFn,
    onSuccess: () => {
      qc.invalidateQueries({ queryKey: ["workout-plans", experience.id] });
      setNewPlanOpen(false);
      setNewPlanTitle("");
    },
  });

  const assignPlan = useMutation({
    mutationFn: async (vars: { planId: string; whopUserId: string }) =>
      assignPlanMutation(experience.id, vars.planId).mutationFn({ whopUserId: vars.whopUserId }),
    onSuccess: () => {
      qc.invalidateQueries({ queryKey: ["workout-plans", experience.id] });
      setAssignUserId("");
    },
  });

  const updatePlan = useMutation({
    mutationFn: async () => {
      if (!editPlanId) return
      return updatePlanMutation(experience.id, editPlanId).mutationFn({ title: editPlanTitle })
    },
    onSuccess: () => {
      qc.invalidateQueries({ queryKey: ["workout-plans", experience.id] });
      setEditPlanId(null);
      setEditPlanTitle("");
    },
  });

  const userPlans = useMemo(() => plans ?? [], [plans]);

  useEffect(() => {
    if (!access) return;
    const isAdmin = access.accessLevel === "owner" || access.accessLevel === "admin";
    const target = isAdmin
      ? `/experiences/${experience.id}/dashboard`
      : `/experiences/${experience.id}/workouts`;
    router.replace(target);
  }, [access, experience.id, router]);

  return (
    <div className="min-h-screen w-full p-6">
      <div className="flex items-center justify-between mb-6">
        <div>
          <div className="text-sm opacity-70">Experience</div>
          <div className="text-xl font-semibold">{experience.name}</div>
        </div>
        {isAdmin && (
          <Dialog.Root open={newPlanOpen} onOpenChange={setNewPlanOpen}>
            <Dialog.Trigger asChild>
              <Button variant="solid">New Plan</Button>
            </Dialog.Trigger>
            <Dialog.Content>
              <Dialog.Title>Create plan</Dialog.Title>
              <div className="mt-4">
                <TextField.Input
                  placeholder="Plan title"
                  value={newPlanTitle}
                  onChange={(e: any) => setNewPlanTitle(e.target.value)}
                />
              </div>
              <div className="mt-4 flex justify-end">
                <Button
                  disabled={!newPlanTitle || createPlan.isPending}
                  onClick={() => createPlan.mutate({ title: newPlanTitle })}
                >
                  {createPlan.isPending ? "Creating..." : "Create"}
                </Button>
              </div>
            </Dialog.Content>
          </Dialog.Root>
        )}
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <Card>
          <div className="p-4">
            <div className="text-sm opacity-70 mb-1">Signed in as</div>
            <div className="font-medium">{user.name} (@{user.username})</div>
            <div className="text-xs opacity-70 mt-1">Access: {access.accessLevel}</div>
          </div>
        </Card>

        <Card>
          <div className="p-4">
            <div className="text-sm opacity-70 mb-1">My Workouts</div>
            {isLoadingPlans ? (
              <div className="text-sm opacity-70">Loading...</div>
            ) : userPlans.length === 0 ? (
              <div className="text-sm opacity-70">No workouts assigned yet.</div>
            ) : (
              <div className="space-y-2">
                {userPlans.map((p) => (
                  <div key={p.id} className="flex items-center justify-between">
                    <div>
                      <div className="font-medium">{p.title}</div>
                      <div className="text-xs opacity-70">
                        {p.daysCount} days â€¢ {p.assignedCount} assigned
                      </div>
                    </div>
                    {isAdmin && (
                      <div className="flex items-center gap-2">
                        <Button size="sm" variant="soft" onClick={() => { setEditPlanId(p.id); setEditPlanTitle(p.title); }}>
                          Edit
                        </Button>
                        <TextField.Input
                          placeholder="Whop user id"
                          value={assignUserId}
                          onChange={(e: any) => setAssignUserId(e.target.value)}
                          style={{ width: 160 }}
                        />
                        <Button
                          size="sm"
                          disabled={!assignUserId || assignPlan.isPending}
                          onClick={() => assignPlan.mutate({ planId: p.id, whopUserId: assignUserId })}
                        >
                          Assign
                        </Button>
                      </div>
                    )}
                  </div>
                ))}
              </div>
            )}
          </div>
        </Card>

        {isAdmin && (
          <Card>
            <div className="p-4">
              <div className="text-sm opacity-70 mb-2">Users</div>
              <div className="mb-3">
                <TextField.Input placeholder="Search users" value={userSearch} onChange={(e: any) => setUserSearch(e.target.value)} />
              </div>
              {isLoadingUsers ? (
                <div className="text-sm opacity-70">Loading users...</div>
              ) : (
                <div className="space-y-2 max-h-80 overflow-auto">
                  {(users ?? []).map((u) => (
                    <div key={u.id} className="flex items-center justify-between text-sm">
                      <div>
                        <div className="font-medium">{u.name || u.username}</div>
                        <div className="opacity-70">{u.username}</div>
                      </div>
                      <Button size="sm" onClick={() => setAssignUserId(u.id)}>Use ID</Button>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </Card>
        )}
      </div>

      {isAdmin && (
        <Dialog.Root open={!!editPlanId} onOpenChange={(open) => { if (!open) { setEditPlanId(null); setEditPlanTitle(""); } }}>
          <Dialog.Content>
            <Dialog.Title>Edit plan</Dialog.Title>
            <div className="mt-4">
              <TextField.Input value={editPlanTitle} onChange={(e: any) => setEditPlanTitle(e.target.value)} />
            </div>
            <div className="mt-4 flex justify-end gap-2">
              <Button variant="soft" onClick={() => { setEditPlanId(null); setEditPlanTitle(""); }}>Cancel</Button>
              <Button disabled={!editPlanTitle || updatePlan.isPending} onClick={() => updatePlan.mutate()}>
                {updatePlan.isPending ? "Saving..." : "Save"}
              </Button>
            </div>
          </Dialog.Content>
        </Dialog.Root>
      )}

      <div className="mt-6 text-sm">
        Need help? <Link className="underline" href="https://docs.whop.com/sdk/whop-api-client" target="_blank" rel="noopener noreferrer">Whop Docs</Link>
      </div>
    </div>
  );
}
